@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@900&family=Roboto&family=Share+Tech+Mono&display=swap');

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.page {
    position: relative;
    width: 100%;
    height: 100vh;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    overflow: hidden;
}

.page-child {
    /* position: fixed; */
    width: 100%;
    height: 100vh;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    overflow: hidden;
}

h1 {
    margin-top: 0;
    margin-bottom: 4rem;
    padding: 0;
    font-family: 'Heebo', sans-serif;
    font-size: 6rem;
    line-height: 8rem;
    text-align: left;
}

.work-title {
    padding: 1.5rem;
    border: 2px solid white;
    font-family: 'Share Tech Mono', monospace;
    font-size: 2rem;
    font-weight: 100;
    letter-spacing: 0.4rem;
    margin-bottom: 2rem;
}

.work-type {
    font-family: 'Share Tech Mono', monospace;
    margin-top: 3rem;
    font-size: 1.5rem;
    font-weight: 100;
    padding-bottom: 1rem;
}

.skill {
    font-family: 'Share Tech Mono', monospace;
    font-size: 1.5rem;
    font-weight: 100;
    margin-top: 2rem;
    flex: 0 0 50%;
}

.skills {
    display: flex;
    flex-wrap: wrap;
    text-align: left;
    padding-bottom: 4rem;
}

.skills-column {
    padding-left: 1rem;
    padding-right: 1rem;
}

img {
    vertical-align: bottom;
}

a {
    color: white !important;
}

a:hover {
    opacity: 0.7;
}

.canvas {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
}

.left {
    left: 0;
}

.right {
    right: 0;
}

.center {
    left: 50%;
    transform: translateX(-50%);
}

.card {
    padding: 3rem;
    box-shadow: none;
    background: rgba(0, 0, 0, 0.5);
    /* background: rgba(36, 33, 89, 0.9); */
    animation-name: card-anim;
    animation-duration: 0.7s;
    animation-delay: 0.1s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
}

.card>* {
    opacity: 0;
    animation-name: card-child-anim;
    animation-duration: 0.7s;
    animation-delay: 0.1s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
}

@media only screen and (max-width: 1024px) {
    h1 {
        font-size: 4.5rem;
        line-height: 6rem;
    }

    .card {
        padding: 2rem;
    }

    .center-content {
        transform: scale(0.8);
    }

    .skill {
        font-size: 1.3rem;
    }
}

@keyframes card-anim {
    0% {
        box-shadow: none;
        /* margin-left: 1rem;
    margin-top: 1rem; */
    }

    50% {
        transform: translate(-1.5rem, -1.5rem);
        box-shadow: 1.5rem 1.5rem 0 0 rgba(0, 0, 0, 0.25);
        /* box-shadow: 1.5rem 1.5rem 0 0 rgba(66, 61, 163, 0.8); */
    }

    100% {
        opacity: 1;
        transform: translate(-1rem, -1rem);
        box-shadow: 1rem 1rem 0 0 rgba(0, 0, 0, 0.25);
        /* box-shadow: 1rem 1rem 0 0 rgba(66, 61, 163, 0.8); */
    }
}

@keyframes card-child-anim {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.moon {
    animation-name: moon-anim;
    animation-duration: 60s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

@keyframes moon-anim {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(360deg);
    }
}

.background-gradient {
    position: fixed;
    top: 0;
    left: 0;
    background: linear-gradient(-45deg, #FFC1A7, #F67E7D, #6CC2BD, #7D7AA2);
    background-size: 400% 400%;
    background-position: 0% 50%;
    animation: background-gradient-anim 15s ease infinite;
    animation-delay: 0.5s;
    width: 100%;
    height: 100vh;
}

@keyframes background-gradient-anim {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}